:root {
    --bg-color: #FFFFFF;
    --bg-color-dull: #F5F5F7;

    --text-color: #1D1D1F;

    --border: hsla(0, 0%, 89%, 0.7);
    --glass-color: 0, 0%, 100%;
    --glass-filter: blur(20px) saturate(180%);
}

/* cursor styles */
.cursor {
    width: 20px;
    aspect-ratio: 1;
    border-radius: 50%;
    background: hsla(0, 0%, 50%, 0.5);
    position: fixed;
    z-index: 100;
    pointer-events: none;
    transition: all 0.1s ease-out;
    transition-property: background, transform, filter;
}

/* navbar */
.navbar {
    width: 100%;
    height: 3.5rem;
    background: hsla(var(--glass-color), 0.7);
    backdrop-filter: var(--glass-filter);
    -webkit-backdrop-filter: var(--glass-filter);
    position: fixed;
    top: 0;
    border-bottom: 1px solid var(--border);

    .nav-logo {
        font-weight: 400;
        display: unset;
        font-size: 1.1rem;
        color: var(--bg-color);
        padding: calc(1.5rem / 2);
        margin: 0 20px;
        line-height: 3.5rem;
        border-radius: 10px;
        background: var(--text-color);
    }
}

.panel {
    width: 100%;
    min-height: 100vh;
    position: relative;
}

/* floating menu */
.floating-button {
    width: 75px;
    height: 55px;
    background: hsla(0, 0%, 60%, 0.3);
    backdrop-filter: var(--glass-filter);
    border-radius: 50px;
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    transition: all 0.2s ease;
    
    svg {
        transform: scale(1.75);
    }
}

.menu-container {
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: grid;
    gap: 15px;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 15px;
    transform: scale(0.2);
    transform-origin: bottom right;
    transition: all 0.2s ease, opacity 0.1s;
    opacity: 0;

    .menu-item {
        width: 180px;
        height: 50px;
        background: hsla(0, 0%, 95%, 0.7);
        backdrop-filter: var(--glass-filter);
        border-radius: 10px;
        grid-column: span 3; 
        display: flex;
        align-items: center;
        text-align: center;
        flex-direction: row;
        p {
            width: 50%;
        }
    }
    .icon {
        width: 50px;
        grid-column: initial;
        justify-content: center;

        svg {
            transform: scale(1.5);
        }
    }
}

.theme-switch {
    position: absolute;
    z-index: -1;
    width: 50%;
    background: #FFFFFF;
    height: 100%;
    border-radius: 10px;
    transition: 0.2s ease;
}

/* home */
.home-container {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
    
    .profile-container {
        width: 10rem;
        height: 10rem;
        border-radius: 50%;
        box-shadow: 0px 0px 50px 10px rgba(52, 187, 180, 0.75);
        img {
            width: 10rem;
            border-radius: 50%;
        }
    }
    .profile-container::before {
        content: '';
        width: 11rem;
        height: 11rem;
        transform: translate(-0.55rem, -0.55rem);
        border-radius: 50%;
        background: hsla(0, 0%, 95%, 0.7);
        backdrop-filter: var(--glass-filter);
        border: 1px solid var(--border);
        position: absolute;
        z-index: -1;
    }
    
    h1 {
        font-weight: 600;
    }
    p {
        opacity: 0.9;
        max-width: 80vw;
    }
}

/* gradient title */
.gradient-container {
    text-align: center;
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    p {
        font-size: 22vw;
        font-weight: 700;
        background-image: linear-gradient(90deg, rgba(10,199,171,1) 10%, rgba(255,252,0,1) 30%, rgba(255,88,39,1) 50%, rgba(255,228,0,1) 70%, rgba(10,199,171,1) 90%);
        background-size: 400%;
        background-clip: text;
        color: transparent;
        animation: gradient 30s alternate infinite;
        margin: 0vh;
        line-height: 1.5;
    }
}

@keyframes gradient {
    0% {
        background-position: left;
    }
    100% {
        background-position: right;
    }
}

/* toolkit */
.toolkit-container {
    background: var(--bg-color-dull);
    height: 100%;
    padding: 1rem 0rem;

    p {
        text-align: center;
        font-size: 6rem;
        font-weight: 600;
    }

    .toolkit-grid {
        margin: 0 auto;
        width: 90vw;
        display: grid;
        gap: 1.2rem;
        grid-template-areas: "pixel" "tech-apps" "pallet" "creative-apps";
        
        div {
            height: 30rem;
            background: var(--bg-color);
            border-radius: 30px;
            border: 1px solid var(--border);
            display: flex;
            justify-content: center;
            align-items: center;
            p {
                font-size: 1.5rem;
                text-align: center;
                font-weight: 400;
                margin: 3rem 0;
            }
        }

        .tech-apps {
            align-items: flex-end;
            grid-area: tech-apps;
        }

        .pixel {
            height: 10rem;
            grid-area: pixel;
            background: linear-gradient(45deg, #16377D, #49C8AB);
            color: #ffffff;
        }

        .creative-apps {
            grid-area: creative-apps;
        }

        .pallet {
            height: 10rem;
            grid-area: pallet;
            background: linear-gradient(-45deg, #9E66E7, #EA4F57);
            color: #ffffff;
        }
    }
}

@media only screen and (min-width: 768px) {
    .floating-button {
        left: 20px;
    }
    .menu-container {
        left: 20px;
        right: inherit;
        transform-origin: bottom left;
    }
    .toolkit-grid {
        grid-template-columns: 1fr 1fr 1fr !important;
        grid-template-areas: "pixel tech-apps tech-apps" "creative-apps creative-apps pallet" !important;
    }
    .pixel, .pallet {
        height: 30rem !important; 
    }
    .gradient-container > p {
        font-size: 7.5vw;
    }
}

.hover-button {
    width: 50px;
    padding: 0.7rem 1.5rem;
    border-radius: 0.5rem;
    background: #fd026b;
    margin: 0 auto;
}